.tracks-wrp {
  flex-grow: 3;
  display: flex;
  flex-direction: column;
}

.tracks__items {
  height: 98%;
  width: min-content;
  display: flex;
  margin-top: 24px;
}

.tracks__control {
  display: flex;
  margin-top: 16px;
  gap: 12px;
}

.tracks__input {
  font-size: 24px;
}

.tracks__btn {
  font-size: 24px;
}

.track {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-width: 300px;
  border: 2px solid black;
  margin-right: 24px;
}

.track-name {
  width: 100%;
  height: min-content;
  text-align: center;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  font-size: 24px;
}

.new-task-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  gap: 5px;
  border: 2px solid black;
  border-radius: 5px;
  padding: 10px 25px;
  background-color: white;
}

.track__control {
  font-size: 24px;
  display: flex;
  border-bottom: 2px solid black;
  padding: 0 5px;
}

.track__btns {
  display: flex;
  gap: 5px;
  align-items: center;
  position: relative;
  z-index: 1;
}

.task__btns {
  display: flex;
  gap: 5px;
  align-items: center;
}

.task {
  border: 2px solid black;
  min-height: 150px;
  margin: 10px 20px 0 20px;
}

.task__control {
  display: flex;
  padding: 0 5px;
  justify-content: space-between;
  border-bottom: 2px solid black;
}

.task-name {
  width: 50%;
  font-size: 24px;
}

.task-desc {
  font-size: 24px;
}

.track__move {
  width: 20px;
  height: 15px;
  background-color: black;
  user-select: none;
}

.add-task-btn {
  font-size: 24px;
  margin: 25px 40px 0px;
}