* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body,
html,
#root {
  height: 100%;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0 24px;
}

.nav {
  display: flex;
  gap: 12px;
  margin-top: 12px;
}

.nav__item {
  font-size: 24px;
  text-decoration: none;
}